
























































































































































import { defineComponent } from "@vue/composition-api";

import { useTable } from "../utils/table";
export default defineComponent({
	name: "mt-display",
	setup(_, ctx) {
		const {
			page,
			pageData,
			pageCount,
			isSearch,
			columns,
			pageDivide,
			pageDivideList,
			sortData,
			labelList,
			sortTemp,
			searchMode,
			currentConfig,
			resetSearchMode,
			tableDisplayFlex,
			tableDisplayFlexList,
			tableFlexBindClass,
			downloadFile,
			downloadOps,
			downloadOp,
			tableData,
		} = useTable(ctx);

		document.title =
			ctx.root.$store.state.globalConfig.title +
			" | " +
			currentConfig.value.title;

		return {
			page,
			pageData,
			pageCount,
			isSearch,
			columns,
			pageDivide,
			pageDivideList,
			sortData,
			labelList,
			sortTemp,
			searchMode,
			currentConfig,
			resetSearchMode,
			tableDisplayFlex,
			tableDisplayFlexList,
			tableFlexBindClass,
			downloadFile,
			downloadOps,
			downloadOp,
			tableData,
		};
	},
});
