



























import { defineComponent, computed } from "@vue/composition-api";
import countDownPanel from "./countdown-panel.vue";

export default defineComponent({
	name: "mt-count-down",
	components: {
		countDownPanel,
	},
	setup(props, ctx) {
		const countDownList = computed(
			() => ctx.root.$store.state.globalConfig.countdown
		);

		const show = computed(
			() =>
				typeof countDownList.value?.date == "string" &&
				typeof countDownList.value?.name == "string"
		);

		document.title = ctx.root.$store.state.globalConfig.title;

		return {
			countDownList,
			show,
		};
	},
});
