































import { defineComponent, ref, computed } from "@vue/composition-api";
import { getCurrentConfig } from "../utils/hooks";

export default defineComponent({
	name: "mt-nav",
	setup(props, ctx) {
		const open = ref(false);

		const { currentConfig, index } = getCurrentConfig(ctx);
		const countDownList = computed(
			() => ctx.root.$store.state.globalConfig.countdown
		);

		const show = computed(
			() =>
				typeof countDownList.value?.date == "string" &&
				typeof countDownList.value?.name == "string"
		);

		const routes = computed(() => {
			const res = [...ctx.root.$router.options.routes!];

			if (!show.value) {
				for (let i = 0; i < res.length; i++) {
					if (res[i].name === "count-down") {
						res[i]!.meta!.hide = true;
					}
				}
			}
			return res;
		});

		function clickOutside(e: HTMLElement) {
			if (e.className !== "mu-ripple-wrapper") {
				open.value = false;
			}
		}

		return {
			open,
			index,
			routes,
			currentConfig,
			clickOutside,
		};
	},
});
