





































import { defineComponent, computed } from "@vue/composition-api";
import { useCountDown } from "../utils/hooks";

export default defineComponent({
	name: "mt-count-down-panel",
	props: {
		name: {
			required: true,
			type: String,
		},
		date: {
			required: true,
			type: String,
		},
		show: {
			default: true,
			type: Boolean,
		},
	},

	setup(props, _) {
		const countDownData = useCountDown(props.date);
		return {
			countDownData,
		};
	},
});
