import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/home.vue";
import List from "../views/list.vue";
import CountDown from "../views/count-down.vue";

Vue.use(VueRouter);

export const routes = [
	{
		path: "/",
		name: "home",
		component: Home,
		meta: {
			name: "",
			icon: "home",
		},
	},
	{
		path: "/list",
		name: "list",
		component: List,
		meta: {
			name: "",
			icon: "assignment",
		},
	},
	{
		path: "/count-down",
		name: "count-down",
		component: CountDown,
		meta: {
			name: "",
			icon: "schedule",
		},
	},
	{
		path: "**",
		redirect: "/",
		meta: {
			hide: true,
		},
	},
];
